import React, {Component} from 'react';
import Snap from 'snapsvg-cjs';
import './SVGGrid.scss';

import COLORS from '../../../../variables';
import Button from "../../../Controls/Button/Button";
import AppContext from "../../../../App-context";

class SVGGrid extends Component {

	constructor() {
		super();
		this.state = {
			element: null,
			gridSize: 15,
			buttons: {
				clear: {
					callback: () => {
						Snap(this.svgDiv).clear();
						this.svgRender();
					},
					label: "Clear",
					style: {
						colorPrimary: COLORS.$grayDark,
						colorSecondary: COLORS.$grayLight
					}
				},
				content: {
					callback: () => {
						this.setElement("content");
						this.drawElement(this.svgDiv, this.state.buttons.content);
					},
					label: "Content",
					style: {
						colorPrimary: COLORS.$colorSecondary_1_4,
						colorSecondary: COLORS.$colorSecondary_1_2
					}
				},
				header: {
					callback: () => {
						this.setElement("header");
						this.drawElement(this.svgDiv, this.state.buttons.header);
					},
					label: "Header",
					style: {
						colorPrimary: COLORS.$colorSecondary_2_4,
						colorSecondary: COLORS.$colorSecondary_2_2
					}
				},
				sidebar: {
					callback: () => {
						this.setElement("sidebar");
						this.drawElement(this.svgDiv, this.state.buttons.sidebar);
					},
					label: "Sidebar",
					style: {
						colorPrimary: COLORS.$colorPrimary_4,
						colorSecondary: COLORS.$colorPrimary_2
					}
				}
			}
		};
	}

	onEnterViewport(id, setCurrentPOC) {
		setCurrentPOC(id);console.log("enter")
	}

	onExitViewport(setCurrentPOC) {
		setCurrentPOC(0);console.log("leave")
	};

	setGridSize(gridSize) {
		this.setState(state => ({gridSize}));
	}

	setElement(element) {
		this.setState(state => ({element}));
	}


	svgRender() {
		//let snap = Snap(this.svgDiv);
		let {height, width} = window.getComputedStyle(this.svgDiv, null),
			snap = Snap(this.svgDiv),
			pattern,
			patternDim,
			path;

		//ToDo update instead of deleting
		snap.clear();

		height = height.replace("px", "");
		width = width.replace("px", "");

		patternDim = {
			height: height / this.state.gridSize,
			width: width / this.state.gridSize
		};

		path = `M0,0H${patternDim.width}V${patternDim.height}H0V0`;
		pattern = snap.path(path).attr({
			fill: "none",
			stroke: COLORS.$grayMedium,
			strokeWidth: .5
		}).pattern(0, 0, patternDim.width, patternDim.height);

		snap.attr({
			viewBox: "0,0," + width + "," + height,
			fill: pattern
		});

		snap.rect(0, 0, width, height).attr({
			fill: pattern
		});
	}

	drawElement(svgNode, element) {
		console.log(element)
		let {colorPrimary, colorSecondary} = element.style,
			x_start,
			x_end,
			y_start,
			y_end,
			gridSize = this.state.gridSize,
			svgDiv = this.svgDiv,
			snap = Snap(svgNode),
			rect_temp;

		function draw_Rect(x_start, x_end, y_start, y_end) {
			return snap.rect(
				x_start >= x_end ? x_end : x_start,
				y_start >= y_end ? y_end : y_start,
				Math.abs(x_end - x_start),
				Math.abs(y_end - y_start)
			);
		}

		function draw_Start(e) {
			svgNode.addEventListener("mousemove", draw_Move);
			x_start = e.offsetX;
			y_start = e.offsetY;
		}

		function draw_Move(e) {
			x_end = e.offsetX;
			y_end = e.offsetY;

			if (rect_temp) {
				rect_temp.remove();
			}
			rect_temp = draw_Rect(x_start, x_end, y_start, y_end);
			rect_temp.attr({
				fill: colorPrimary,
				stroke: colorSecondary,
				"stroke-dasharray": "10,10",
				strokeWidth: 1,
				opacity: .5
			});
		}

		function draw_Stop() {
			let rect, text;

			let {height, width} = window.getComputedStyle(svgDiv, null);

			height = height.replace("px", "");
			width = width.replace("px", "");
			height = height / gridSize;
			width = width / gridSize;

			x_start = Snap.snapTo(width, x_start, width / 2 + 1);
			x_end = Snap.snapTo(width, x_end, width / 2 + 1);
			y_start = Snap.snapTo(height, y_start, height / 2 + 1);
			y_end = Snap.snapTo(height, y_end, height / 2 + 1);

			if (rect_temp) {
				rect_temp.remove();
			}
			svgNode.removeEventListener("mousemove", draw_Move);

			//Grid size is the distance of each grid element
			rect = draw_Rect(x_start, x_end, y_start, y_end);
			text = snap.text(rect.getBBox().x + height / 2, rect.getBBox().y + width / 2, element.label);


			console.log(x_start, rect.getBBox().x)

			rect.attr({
				fill: colorPrimary,
				stroke: colorSecondary,
				strokeWidth: 1,
				class: element.label,
				"data-x_start": Math.round(x_start / width),
				"data-x_end": Math.round(x_end / width),
				"data-y_start": Math.round(y_start / height),
				"data-y_end": Math.round(y_end / height)
			});

			//debugger;
			text.attr({
				fill: colorSecondary,
				fontFamily: "sans-serif",
				fontSize: `${gridSize * 2}px`,
				stroke: colorSecondary
			});

			svgNode.removeEventListener("mousedown", draw_Start);
			svgNode.removeEventListener("mouseup", draw_Stop);
			svgNode.style.cursor = "default";
		}

		svgNode.addEventListener("mousedown", draw_Start);	//svgNode = area
		svgNode.addEventListener("mouseup", draw_Stop);
		svgNode.style.cursor = "crosshair";
	};


	componentDidMount() {
		this.svgRender();
	}

	componentDidUpdate() {
		//this.svgRender();
	}

	componentWillUnmount() {
		//ToDo delete svg
	}

	render() {

		return (
			<AppContext.Consumer>
				{({appState, setCurrentPOC}) => {
					return (
						<React.Fragment>
							<ScrollTrigger component="h2"
										   onEnter={() => this.onEnterViewport(1, setCurrentPOC)}
										   onExit={() => this.onExitViewport(setCurrentPOC)}
							>TEST</ScrollTrigger>
							<Button {...this.state.buttons.header}/>
							<Button {...this.state.buttons.sidebar}/>
							<Button {...this.state.buttons.content}/>
							<Button {...this.state.buttons.clear}/>
							<svg className="gridWrapper" ref={d => this.svgDiv = d}/>
						</React.Fragment>
					)
				}}

			</AppContext.Consumer>
		)
	}
}

export default SVGGrid;
